<template>
  <div
    class="d-flex align-center justify-center"
    :class="{ 'content-wrapper': !$root.isMobile }"
  >
    <div
      class="d-flex flex-column white border-radius px-5 pb-10 pt-2 pa-sm-12 content"
      :class="{ 'box-shadow': !$root.isMobile }"
    >
      <h1 class="text-center font-weight-normal sign__up">
        Sign up
      </h1>

      <v-form
        ref="form"
        v-model="form.valid"
        class="form"
        lazy-validation
        @keyup.enter.native="onSignUp"
      >
        <base-field-input
          label="Invitation Token"
          :is-required="true"
          placeholder="Enter Invitation Token"
          v-model.trim="form.values.invitation_token"
          :rules="form.invitationTokenRules"
          :error="Boolean(serverErrors && serverErrors.invitation_token)"
          :error-messages="serverErrors.invitation_token"
          :color="
            form.invitation_token.loading ? 'primary' :
            form.invitation_token.error ? 'error' :
            !form.invitation_token.error && form.invitation_token.checked ? 'success' :
            'primary'"
          validate-on-blur
        >
          <template #append>
            <v-progress-circular
              v-if="form.invitation_token.loading"
              class="full-width"
              :size="23"
              :width="3"
              style="width: 20px;"
              color="primary"
              indeterminate
            />

            <v-icon
              v-else-if="form.invitation_token.checked"
              :color="form.invitation_token.error ? 'error' : 'success'"
              size="23"
            >
              {{ form.invitation_token.error ? 'mdi-information' : 'mdi-check' }}
            </v-icon>
          </template>
        </base-field-input>

        <base-field-input
          label="Full Name"
          :is-required="true"
          placeholder="Enter Full Name"
          :disabled="form.invitation_token.error || !form.invitation_token.checked"
          v-model.trim="form.values.name"
          :rules="form.fullNameRules"
          :error="Boolean(serverError)"
          validate-on-blur
        />

        <base-field-input
          label="Email"
          :is-required="true"
          type="email"
          placeholder="Enter Email"
          :disabled="form.invitation_token.error || !form.invitation_token.checked"
          v-model.trim="form.values.email"
          :rules="form.emailRules"
          :error="Boolean((serverErrors && serverErrors.email) || serverError)"
          :error-messages="serverErrors.email"
          validate-on-blur
        />

        <base-field-input
          label="Phone"
          :is-required="true"
          mask="+1 (###) ###-####"
          placeholder="Enter Phone"
          :disabled="form.invitation_token.error || !form.invitation_token.checked"
          v-model.trim="form.values.phone"
          :rules="form.phoneRules"
          :error="Boolean(serverError)"
          validate-on-blur
        />

        <base-field-input
          label="Password"
          :is-required="true"
          placeholder="Enter password"
          :disabled="form.invitation_token.error || !form.invitation_token.checked"
          v-model.trim="form.values.password"
          :rules="form.passwordRules"
          :type="isPasswordVisible ? 'text' : 'password'"
          :append-icon="isPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
          validate-on-blur
          :error="Boolean(serverError)"
          @click:append="isPasswordVisible = !isPasswordVisible"
        />

        <base-field-input
          label="Confirm Password"
          :is-required="true"
          placeholder="Enter Confirm Password"
          :disabled="form.invitation_token.error || !form.invitation_token.checked"
          v-model.trim="form.confirmPassword"
          :rules="[...form.confirmPasswordRules, sameAsPassword]"
          :type="isConfirmPasswordVisible ? 'text' : 'password'"
          :append-icon="isConfirmPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
          validate-on-blur
          :error="Boolean(serverError)"
          :error-messages="serverError"
          @click:append="isConfirmPasswordVisible = !isConfirmPasswordVisible"
        />
      </v-form>

      <div class="mt-7 d-flex flex-column-reverse flex-sm-row justify-space-between">
        <v-btn
          color="primary"
          class="text-uppercase custom__btn mt-5 mt-sm-0"
          depressed
          outlined
          :to="{ name: this.$constants.ROUTE_NAMES['SIGN_IN']}"
          min-width="215"
          large
          @click="toBack"
        >
          <img
            class="account-icon mr-2"
            :src="require('@images/icons/drivers/back.svg')"
          />
          <span class="font-weight-bold">GO BACK</span>
        </v-btn>
        <v-btn
          color="primary"
          class="text-uppercase custom__btn"
          min-width="215"
          depresse
          large
          :disabled="!form.valid || form.invitation_token.error || !form.invitation_token.checked"
          :loading="isLoading"
          @click="onSignUp"
        >
          Sign Up
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import debounce from 'debounce';
import { omit } from 'ramda';
import { createNamespacedHelpers } from 'vuex';
import BaseFieldInput from '@components/BaseFieldInput';
import { actions } from '@modules/auth/constants';

import {
  required,
  email,
  same,
} from '@utils/helpers/validationRules';

const { mapActions, mapState } = createNamespacedHelpers('auth');

export default {
  name: 'SignUpDispatcher',
  components: {
    BaseFieldInput,
  },
  watch: {
    'form.values.invitation_token': {
      handler(next) {
        return next && this.prefillDispatcherData(next);
      },
    },
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data() {
    return {
      isPasswordVisible: false,
      isConfirmPasswordVisible: false,
      form: {
        invitation_token: {
          loading: false,
          checked: false,
          error: false,
        },
        values: {
          name: '',
          email: '',
          phone: '',
          invitation_token: '',
          password: '',
        },
        confirmPassword: '',
        valid: true,
        fullNameRules: [required('Full Name')],
        invitationTokenRules: [required('Invitation Token')],
        emailRules: [required('Email'), email],
        phoneRules: [required('Phone')],
        passwordRules: [required('Password')],
        confirmPasswordRules: [required('Confirm Password')],
      },
      serverError: '',
      serverErrors: {},
    };
  },
  computed: {
    sameAsPassword() {
      return this.same('Passwords', this.form.values.password);
    },
    ...mapState(['isLoading']),
  },
  mounted() {
    this.form.values.invitation_token = this.$route.query.token;
  },
  methods: {
    resetFormData() {
      this.form.values = {
        ...this.form.values,
        name: '',
        email: '',
        phone: '',
        password: '',
      };
    },
    prefillDispatcherData: debounce(function bounce(token) {
      this.form.invitation_token.loading = true;
      this.form.invitation_token.error = false;
      this.form.invitation_token.checked = false;
      axios.get(`/dispatchers/invited/${token}`, { requestName: 'dispatcher' })
        .then(({ data }) => {
          this.form.values = {
            ...this.form.values,
            ...omit(['id', 'status'], data),
          };
          this.form.invitation_token.error = false;
        })
        .catch(() => {
          this.resetFormData();
          this.$nextTick(() => {
            this.form.invitation_token.error = true;
            this.serverErrors = {
              invitation_token: 'There is no dispatcher with this token',
            };
          });
        })
        .finally(() => {
          this.form.invitation_token.loading = false;
          this.form.invitation_token.checked = true;
        });
    }, 250),
    same,
    onSignUp() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;

      this.signup({
        role: this.$constants.ROLES.DISPATCHER,
        data: { ...this.form.values },
        vm: this,
      }).catch((errors) => {
        if (Array.isArray(errors)) {
          this.serverError = errors;
        } else {
          this.serverErrors = errors;
        }
      });
    },
    ...mapActions({
      signup: actions.SIGN_UP,
    }),
  },
};
</script>

<style lang="scss" scoped>
  .form {
    width: 100%;
  }

  .content-wrapper {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .content {
    max-width: 542px;
    width: 100%;
  }

  .v-input--selection-controls {
    margin-top: 0;
    padding-top: 0;
  }

  .v-input--radio-group--row .v-input--radio-group__input {
    justify-content: center;
  }

  .sign__up {
    font-size: 28px;
    line-height: 150%;
    /* identical to box height, or 42px */

    text-align: center;
    letter-spacing: 0.03em;

    color: #174489;
  }
</style>
